/*Neue Haas Grotesk Display Pro
font-family: neue-haas-grotesk-display, sans-serif;
font-weight: 500,700, 900;*/
html {
	scroll-behavior: smooth;
}

html,
body {
	padding: 0;
	margin: 0;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

}

body,
a,
h1,
h2,
h3,
h4,
h5,
h6,
p {
	font-family: neue-haas-grotesk-display, sans-serif;
}


/* Home Page Styles */
.wrapper {
	min-height: 800px;

}

.second-wrapper {
	min-height: 700px;

}

.learn-more:hover {
	color: white;
}

#leftSideContent {
	bottom: 0;
	position: absolute;
	max-width: 600px;
	margin: 0px 0px 100px 100px;

}

#leftSideContent p {
	font-size: 24px !important;
	font-weight: 300;
}

#leftSide {
	background: #4F7ED5;
	min-height: 100%;
	color: white;
	position: relative;
	z-index: 1;
	background-image: url(../img/squeeze.jpg);
	background-position: bottom;
	background-repeat: no-repeat;
	background-size: cover;

}

#wipeoutPacketContent {
	background: #2F4D84;
	position: relative;
}

#secondRightSide {
	z-index: 25;
}

#secondRightSide p {
	color: #7B7777;
	line-height: 2;
	font-weight: 400;
}

.arne-duncan {
	font-size: 25px;
	font-weight: 900 !important;
	font-style: normal;
	color: black !important;

}

#here {
	font-weight: 900;
	color: #4F7ED5;
	text-decoration: underline;
}

.packet-showcase {
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
}

.packet-showcase .og-packet {
	max-width: 40%;
}

#secondContainer {
	padding-top: 80px;
}



/* End */


/*	Global Styles - styles that apply to all pages   */


